.react-quiz-container {
  margin: 20px;
  width: 100%;
  flex-flow: row;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  font-family: 'Roboto', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 120%;
  /* width: 500px; */
}

.react-quiz-container .startQuizWrapper {
  margin-top: 10px;
  
  flex-flow: row;
  flex-direction: row;
  align-items: center;
}

.react-quiz-container .btn {
  margin-bottom: 0;
  font-weight: 600;
  text-align: center;
  /* padding: 11px 12px; */
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  /* white-space: nowrap; */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
}

.react-quiz-container .questionWrapper .btn {
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 4px;
  border: 1px solid #e8e8e8;;
  /* font-size: 15px; */
  display: inline-block;
  
}

.react-quiz-container .questionWrapper .btn.correct {
  background: green;
  color: white;
}



.react-quiz-container .questionModal .alert {
  padding: 20px;
  margin-bottom: 21px;
  border: 1px solid transparent;
  border-radius: 2px;
  color: #fff;
  text-align: center;
}

.react-quiz-container .correct {
  background: green;
  animation: bounce 0.75s ease ;
}

@keyframes bounce{
  from {top: 106px;}
  50%  {top: 86px;}
  to   {top: 106px;}
}


.react-quiz-container .incorrect {
  background: red;
  animation: shake 0.5s cubic-bezier(.35,.05,.20,.99) both;
}

.react-quiz-container .questionWrapper img {
  width: 300px;
}

.react-quiz-container .answerBtn {
  width:100%;
}

.react-quiz-container .startQuizWrapper .startQuizBtn {
  font-size: 120%;
  /* border-radius: 500px;
  line-height: 1.35135;
  color: rgba(0,0,0,0.65);
  background-color: #fff;
  border: 1px solid #d9d9d9; */
  animation: shake 1.75s ease infinite;
}

.react-quiz-container .result-answer-wrapper {
  margin-bottom: 20px;
  border: 1px solid #e8e8e8;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  flex-direction: row;
}

.react-quiz-container .result-answer-wrapper h3 {
    background-color: #fafafa;;
    opacity: 0.8;
    color: rgba(0,0,0,0.9);
    margin: 0px;
    padding: 10px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.react-quiz-container .result-answer-wrapper .explaination {
  padding: 20px;
  margin: 0px 20px 20px 20px;
  border: 1px solid #e8e8e8;
}


.react-quiz-container .result-answer {
  padding: 20px;
}

.react-quiz-container .quiz-synopsis {
  margin: 15px 0px;
}

.react-quiz-container .quiz-result-filter {
   overflow: hidden;
   width: 120px;
   -webkit-border-radius: 5px;
   -moz-border-radius: 5px;
   border-radius: 5px;
   background-color: #fefefe;
   margin-bottom: 10px;
   border: 1px solid #e8e8e8;
}

.react-quiz-container .quiz-result-filter select {
   background: transparent;
   border: none;
   font-size: 16px;
   padding: 5px;
   width: 100%;
   height: 30px;
   border: 1px solid #ffffff;
}



.react-quiz-container .quiz-question-wrapper {
  flex-direction: row;
  flex-flow: row;
  display: flex;
  flex-wrap: wrap;
  /* align-items: center; */
  justify-content: space-around;
  overflow-wrap: break-word;
  animation: slide-in .4s ease;
  font-size: 30px;
}

.react-quiz-container .nextQuestionBtn {
  display: inline-block;
}


.react-quiz-container .questionButtonWrapper {
  text-align: center;
  width: 100%;
  margin:0 auto;
  overflow-wrap: break-word;
  margin-top : 50px;
}


@keyframes shake {
  10%, 90% {
    transform: translateX(-1px);
  }
  20%, 80% {
    transform: translateX(1px);
  }
  30%, 50%, 70% {
    transform: translateX(-2px);
  }
  45%, 55% {
    transform: translateX(2px);
  }
}

@keyframes modal-enter {
  0% {
    visibility: visible;
    opacity: 1;
  }
  50% {
    transform: scale(1);
    opacity: 1;
  }
  85% {
    opacity: 1;
    transform: scale(1);
  }
  99% {
    opacity: .5;
    transform: scale(1);
  }
  100% {
    display: none;
    transform: scale(1);
  }
}

@keyframes slide-in {
  0%   { opacity: 0; transform: translate3d(40%, 0, 0); }
  100% { opacity: 1; transform: none; }
}


button,
a {
    display: inline-block;
    text-decoration: none;
    font-family: 'Ubuntu', sans-serif;
    background: rgba(0, 0, 0, 0.4);
    color: #fff;
    padding: 10px 20px;
    font-size: 30px;
    border: 2px solid #fff;
    margin: 8px;
    -ms-transform: scale(0.9);
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
    box-shadow: 4px 4px rgba(0, 0, 0, 0.4);
    transition: transform 0.2s cubic-bezier(0.34, 1.65, 0.88, 0.925) 0s;
    outline: 0;
}

