  font-size: 120%;
  /* width: 500px; */
}

.react-quiz-container .startQuizWrapper {
  margin-top: 10px;
  
  flex-flow: row;
  flex-direction: row;
  align-items: center;
}

.react-quiz-container .btn {
  margin-bottom: 0;
  font-weight: 600;
  text-align: center;
  /* padding: 11px 12px; */
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  /* white-space: nowrap; */
