}

@-webkit-keyframes modal-enter {
  0% {
    visibility: visible;
    opacity: 1;
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  85% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  99% {
    opacity: .5;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    display: none;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes modal-enter {
  0% {
    visibility: visible;
    opacity: 1;
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  85% {
    opacity: 1;
