  }
}

@-webkit-keyframes slide-in {
  0%   { opacity: 0; -webkit-transform: translate3d(40%, 0, 0); transform: translate3d(40%, 0, 0); }
  100% { opacity: 1; -webkit-transform: none; transform: none; }
}

@keyframes slide-in {
  0%   { opacity: 0; -webkit-transform: translate3d(40%, 0, 0); transform: translate3d(40%, 0, 0); }
  100% { opacity: 1; -webkit-transform: none; transform: none; }
}


button,
a {
    display: inline-block;
    text-decoration: none;
    font-family: 'Ubuntu', sans-serif;
    background: rgba(0, 0, 0, 0.4);
    color: #fff;
    padding: 10px 20px;
    font-size: 30px;
    border: 2px solid #fff;
    margin: 8px;
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
    box-shadow: 4px 4px rgba(0, 0, 0, 0.4);
    transition: -webkit-transform 0.2s cubic-bezier(0.34, 1.65, 0.88, 0.925) 0s;
    transition: transform 0.2s cubic-bezier(0.34, 1.65, 0.88, 0.925) 0s;
    transition: transform 0.2s cubic-bezier(0.34, 1.65, 0.88, 0.925) 0s, -webkit-transform 0.2s cubic-bezier(0.34, 1.65, 0.88, 0.925) 0s;
    outline: 0;
}

